import { graphql } from 'gatsby';
import { useTranslation } from 'gatsby-plugin-react-i18next';
import Layout from '../pages/layout';
import Post from '../components/Blog';
import { PageProps } from 'gatsby';
import React from 'react';
import { Company } from '../interfaces';
import data from "../data/data.json";

const info: Company = data[0];

const PostDetailPage = ({ data }: PageProps<{
    postsJson: any; post: { title: string; date: string; content: string } 
}>) => {
  const { t } = useTranslation();
  const post = data.postsJson;

  if (!post) {
    return <div>{t('Post not found')}</div>;
  }

  return (
    <Layout data={info}>
      <Post title={post.title} date={post.date} content={post.content} slug={post.slug} />
    </Layout>
  );
};

export const query = graphql`
  query PostDetailPageQuery($slug: String!, $language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    postsJson(slug: { eq: $slug }) {
      title
      date
      content
      slug
    }
  }
`;

export default PostDetailPage;